import { Component, OnInit } from '@angular/core';
import { appConfig } from 'src/app/global.config';
import { ImageService } from 'src/app/_services';

@Component({
  selector: 'app-solution-security',
  templateUrl: './solution-security.component.html',
  styleUrls: ['./solution-security.component.css', './responsive.css']
})
export class SolutionSecurityComponent implements OnInit {

  imagePath = this.imageService.getStaticImage('');
  contentLoaded: any = [];
  dashboardUrl = appConfig.dashboardUrl;
  constructor(
    private imageService: ImageService
  ) { }

  ngOnInit(): void {
  }
  getStaticImage(name) {
    return this.imageService.getStaticImage(name);
  }
  loaded(item) {
    return this.contentLoaded[item] = true;
  }

  isLoaded(item) {
    return { visibility: !this.contentLoaded[item] ? 'hidden' : '' }
  }
}
